import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import profilepic from "./images/Yearbook1.jpg";
import List from '@material-ui/core/List';
import HelpIcon from '@material-ui/icons/Help';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import Divider from '@material-ui/core/Divider';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { createMuiTheme } from '@material-ui/core/styles';
import VideogameAssetIcon from '@material-ui/icons/VideogameAsset';
import NavigationBar from './NavigationBar';
import './App.css';
import '@fontsource/roboto';


const theme = createMuiTheme({
  palette: {
    primary: {
      light: '#b6e3ff',
      main: '#82b1ff',
      dark: '#4d82cb',
      contrastText: '#000',
    },
    secondary: {
      light: '#ffad42',
      main: '#f57c00',
      dark: '#bb4d00',
      contrastText: '#000',
    },
  },
});

const colors = theme.palette;

export default class App extends React.Component {

  constructor(props) {
    super(props);
  }



  handleProjectListClick = (event, index) => {
    for (let i = 0; i < document.getElementsByClassName("project").length; i += 1) {
      document.getElementsByClassName("project")[i].style.backgroundColor = "#d1d1d1";
    }
    document.getElementsByClassName("project")[index].style.backgroundColor = "#bdbdbd";
  }


  render() {
    return (
      <div className="homepage">
        <NavigationBar />

        {/* // ANCHOR About me */}
        <div id="parallax">
          <div id="aboutme">
            <div id="aboutmeText">
              <h1>Tom Han</h1>
              <div>Hello there! My name is Tom and I am a freshman at the University of Waterloo currently enrolled in the
                computer science co-op program. I'm very passionate for software engineering, and I'm seeking internships
                for the Spring 2022 term where I can apply my programming skills and learn more about software development.
              </div>
            </div>
            <Avatar alt="Tom Han"
              className="profilepic"
              src={profilepic}
              style={{
                width: "250px",
                height: "250px",
                border: "5px solid",
                borderColor: colors.primary.main,
                boxShadow: "0px 0px 10px"
              }} />
          </div>
        </div>


        {/* ANCHOR Project List  */}
        <div id="projects">
          PROJECTS
        </div>
        <List id="projectList">
          <ListSubheader component="div">
            Projects
          </ListSubheader>
          <Divider />
          <ListItem
            button
            className = "project"
            onClick={(event) => this.handleProjectListClick(event, 0)}
          >
            <ListItemIcon>
              <VideogameAssetIcon />
            </ListItemIcon>
            <ListItemText primary="Northern Hackers" secondary="Multiplayer game where you have to cut down more trees than your friends" />
          </ListItem>

          <Divider />

          <ListItem
            button
            className = "project"
            onClick={(event) => this.handleProjectListClick(event, 1)}
          >
            <ListItemIcon>
              <VideogameAssetIcon />
            </ListItemIcon>
            <ListItemText primary="Blast to the Past" secondary="Puzzle game with time travel mechanics" />
          </ListItem>

          <Divider />

          <ListItem
            button
            className = "project"
            onClick={(event) => this.handleProjectListClick(event, 2)}
          >
            <ListItemIcon>
              <VideogameAssetIcon />
            </ListItemIcon>
            <ListItemText primary="Skelascent and Skelascent Mobile" secondary="Endlessly scrolling platforming game" />
          </ListItem>

          <Divider />

          <ListItem>
            <ListItemIcon>
              <HelpIcon />
            </ListItemIcon>
            <ListItemText primary="???" secondary="More to come..." />
          </ListItem>

        </List>



      </div>
    );
  }

}

